<template>
  <div class="app-theme-control" :class="theme" @click.stop="clickControl">
    <button class="tw-flex" type="button">
      <svg xmlns="http://www.w3.org/2000/svg" :width="width" :height="height" fill="currentColor" class="bi bi-brightness-high-fill" viewBox="0 0 16 16">
        <path
          d="M12 8a4 4 0 1 1-8 0 4 4 0 0 1 8 0zM8 0a.5.5 0 0 1 .5.5v2a.5.5 0 0 1-1 0v-2A.5.5 0 0 1 8 0zm0 13a.5.5 0 0 1 .5.5v2a.5.5 0 0 1-1 0v-2A.5.5 0 0 1 8 13zm8-5a.5.5 0 0 1-.5.5h-2a.5.5 0 0 1 0-1h2a.5.5 0 0 1 .5.5zM3 8a.5.5 0 0 1-.5.5h-2a.5.5 0 0 1 0-1h2A.5.5 0 0 1 3 8zm10.657-5.657a.5.5 0 0 1 0 .707l-1.414 1.415a.5.5 0 1 1-.707-.708l1.414-1.414a.5.5 0 0 1 .707 0zm-9.193 9.193a.5.5 0 0 1 0 .707L3.05 13.657a.5.5 0 0 1-.707-.707l1.414-1.414a.5.5 0 0 1 .707 0zm9.193 2.121a.5.5 0 0 1-.707 0l-1.414-1.414a.5.5 0 0 1 .707-.707l1.414 1.414a.5.5 0 0 1 0 .707zM4.464 4.465a.5.5 0 0 1-.707 0L2.343 3.05a.5.5 0 1 1 .707-.707l1.414 1.414a.5.5 0 0 1 0 .708z"
        />
      </svg>
    </button>
  </div>
</template>

<script>
export default {
  props: {
    width: {
      type: String,
      required: true,
    },
    height: {
      type: String,
      required: true,
    },
  },
  name: "AppThemeControl",
  data() {
    return {
      isDark: true,
    };
  },
  computed: {
    theme() {
      return this.isDark ? "lighter-control" : "darker-control";
    },
  },
  mounted() {
    // pass
  },
  methods: {
    clickControl(event) {
      this.isDark = !this.isDark;
      this.$emit("change", this.isDark, event);
    },
  },
};
</script>

<style lang="sass" scoped>
.app-theme-control:hover
  /* pass */

.darker-control
  @apply tw-text-very-dark-mostly-black-800

.lighter-control
  @apply tw-text-vivid-orange-600

button:hover
  color: inherit !important
</style>
